      //ãƒ†ã‚­ã‚¹ãƒˆå–å¾— æŠ¼ã•ã‚ŒãŸæˆ»ã™ãƒœã‚¿ãƒ³ã®divã‚¿ã‚°è¦ç´ ã®æœ€åˆã®è¦ç´ ã®liã‚¿ã‚°å†…ã«ã‚ã‚‹ãƒ†ã‚­ã‚¹ãƒˆã‚’å–å¾—
      const text = backButton.parentNode.firstElementChild.innerText;
